<template>
  <div class="panel bar">
    <div class="panel-footer" ref="shoPrate"></div>
  </div>
</template>

<script>
import elementResizeDetectorMaker from "element-resize-detector";
export default {
  data() {
    return {
      echartInstance: {},
      options: {
        title: {
          text: "商品销售占比(动态数据)",
          left: "center", //柱状图居中是left:center
          textStyle: {
            color: "rgba(255,255,255,0.8)",
            fontSize: 14,
            fontWeight: "bold",
          },
        },
        color: ["#2f89cf"],
        // 提示框组件
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow", // 默认为直线，可选为：'line' | 'shadow'
          },
        },
        // 修改图表位置大小
        grid: {
          left: "5%",
          top: "15%",
          right: "5%",
          bottom: "5%",
          containLabel: true,
        },
        // x轴相关配置
        xAxis: [
          {
            type: "category",
            data: [
              "冷冻食品类",
              "饮料食品类",
              "糖果糕点类",
              "炒货蜜饯类",
              "调味品类",
              "烟酒茶类",
              "软包装食品",
            ],
            axisTick: {
              alignWithLabel: true,
            },
            // 修改刻度标签，相关样式
            axisLabel: {
              color: "rgba(255,255,255,0.8)",
              fontSize: 10,
              interval: 0, // 0表示全部显示
              formatter: (value) => {
                if (window.innerWidth <= 1500) {
                  // 如果标签长度超过3，将剩余的文字替换为省略号
                  if (value.length > 3) {
                    return value.slice(0, 3) + "..";
                  } else {
                    return value;
                  }
                } else {
                  return value;
                }
              },
            },
            // x轴样式不显示
            axisLine: {
              show: false,
            },
          },
        ],
        // y轴相关配置
        yAxis: [
          {
            type: "value",
            // 修改刻度标签，相关样式
            axisLabel: {
              color: "rgba(255,255,255,0.6)",
              fontSize: 12,
            },
            // y轴样式修改
            axisLine: {
              lineStyle: {
                color: "rgba(255,255,255,0.6)",
                width: 2,
              },
            },
            // y轴分割线的颜色
            splitLine: {
              lineStyle: {
                color: "rgba(255,255,255,0.1)",
              },
            },
          },
        ],
        // 系列列表配置
        series: [
          {
            name: "商品销售占比",
            type: "bar",
            barWidth: "35%",
            // ajax传动态数据
            data: [1000, 1500, 500, 2700, 1300, 800, 3000],
            itemStyle: {
              // 修改柱子圆角
              barBorderRadius: 5,
            },
          },
        ],
      },
      timer: null,
    };
  },
  props: {
    dataechatrs: {
      type: Array,
      default: () => [],
    },
  },
  mounted() {
    this.initEcharts();
  },
  created() {},
  watch: {
    dataechatrs: {
      handler(newVal) {
        // this.$nextTick(() => {
        //   this.options.series[0].data = newVal;
        //   this.initEcharts();
        // });
        this.timer = setTimeout(() => {
          this.options.series[0].data = newVal;
          this.initEcharts();
        }, 2500);
      },
      immediate: true,
    },
  },
  beforeDestroy() {
    this.disposeEcharts();
    clearTimeout(this.timer);
  },
  methods: {
    // 初始化图表
    initEcharts() {
      if (this.$refs.shoPrate) {
        this.echartInstance = this.$echarts.init(this.$refs.shoPrate);
        const option = this.options;
        option && this.echartInstance.setOption(option);
        this.reloadEcharts();
      }
    },
    //监听窗口是否变动
    reloadEcharts() {
      window.onresize = () => {
        this.echartInstance.resize();
      };

      //根据当前容器的尺寸变化而让图表也跟着变化
      let changeSize = elementResizeDetectorMaker();
      changeSize.listenTo(this.$refs.shoPrate, () => {
        this.$nextTick(() => {
          this.echartInstance.resize();
        });
      });
    },
    // 销毁图表以及重置各个数据
    disposeEcharts() {
      if (this.echartInstance) {
        this.echartInstance.dispose();
      }
    },
  },
};
</script>

<style lang="less" scoped>
.panel {
  position: relative;
  height: 3.75rem;
  border: 1px solid rgba(25, 186, 139, 0.17);
  background: url(@/assets/line.png) rgba(255, 255, 255, 0.03);
  padding: 0 0.1875rem 0.5rem;
  margin-bottom: 0.1875rem;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 10px;
    height: 10px;
    border-left: 2px solid #02a6b5;
    border-top: 2px solid #02a6b5;
  }
  &::after {
    position: absolute;
    top: 0;
    right: 0;
    width: 10px;
    height: 10px;
    border-right: 2px solid #02a6b5;
    border-top: 2px solid #02a6b5;
    content: "";
  }
  .panel-footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    min-width: 3.75rem;
    &::before {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 10px;
      height: 10px;
      border-left: 2px solid #02a6b5;
      border-bottom: 2px solid #02a6b5;
      content: "";
    }
    &::after {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 10px;
      height: 10px;
      border-right: 2px solid #02a6b5;
      border-bottom: 2px solid #02a6b5;
      content: "";
    }
  }
  h2 {
    height: 0.6rem;
    color: #fff;
    line-height: 0.6rem;
    text-align: center;
    font-size: 0.25rem;
    font-weight: 400;
  }
}
</style>
