<template>
  <div class="panel bar">
    <div class="panel-footer" ref="salesChange"></div>
  </div>
</template>

<script>
import elementResizeDetectorMaker from "element-resize-detector";
export default {
  data() {
    return {
      options: {
        title: {
          text: "销量变化",
          left: "center", //柱状图居中是left:center
          textStyle: {
            color: "rgba(255,255,255,0.8)",
            fontSize: 14,
            fontWeight: "bold",
          },
        },
        grid: {
          top: "10%",
          left: "25%",
          bottom: "10%",
          right: "15%",
          // containLabel: true,
        },
        xAxis: {
          // 不显示x轴相关信息
          show: false,
        },
        yAxis: [
          {
            type: "category",
            // y轴数据反转，与数组的顺序一致
            inverse: true,
            // 不显示y轴线和刻度
            axisLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            // 将刻度标签文字设置为白色
            axisLabel: {
              color: "#fff",
            },
            data: [
              "冷冻食品类",
              "饮料食品类",
              "糖果糕点类",
              "炒货蜜饯类",
              "调味品类",
            ],
          },
          {
            // y轴数据反转，与数组的顺序一致
            inverse: true,
            show: true,
            // 不显示y轴线和刻度
            axisLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            // 将刻度标签文字设置为白色
            axisLabel: {
              color: "#fff",
            },
            data: [562, 330, 410, 723, 124],
          },
        ],
        series: [
          {
            // 第一组柱子（条状）
            name: "条",
            type: "bar",
            // 柱子之间的距离
            barCategoryGap: 50,
            // 柱子的宽度
            barWidth: 10,
            // 层级 相当于z-index
            yAxisIndex: 0,
            // 柱子更改样式
            itemStyle: {
              barBorderRadius: 20,
              // 此时的color可以修改柱子的颜色
              color: function (params) {
                // params 传进来的是柱子的对象
                // dataIndex 是当前柱子的索引号
                // console.log(params);
                // 声明颜色数组
                let myColor = [
                  "#f29917",
                  "#24af3a",
                  "#4f82ff",
                  "#e00554",
                  "#8B78F6",
                ];
                return myColor[params.dataIndex];
              },
            },
            data: [70, 34, 60, 78, 69],
            // 显示柱子内的百分比文字
            label: {
              show: true,
              position: "inside",
              // {c} 会自动解析为数据（data内的数据）
              formatter: "{c}%",
            },
          },
          {
            // 第二组柱子（框状 border）
            name: "框",
            type: "bar",
            // 柱子之间的距离
            barCategoryGap: 50,
            // 柱子的宽度
            barWidth: 14,
            // 层级 相当于z-index
            yAxisIndex: 1,
            // 柱子修改样式
            itemStyle: {
              color: "none",
              borderColor: "#4e7fff",
              borderWidth: 2,
              barBorderRadius: 15,
            },
            data: [100, 100, 100, 100, 100],
          },
        ],
      },
    };
  },
  mounted() {
    this.initEcharts();
  },
  methods: {
    // 初始化图表
    initEcharts() {
      if (this.$refs.salesChange) {
        this.echartInstance = this.$echarts.init(this.$refs.salesChange);
        const option = this.options;
        option && this.echartInstance.setOption(option);
        this.reloadEcharts();
      }
    },
    //监听窗口是否变动
    reloadEcharts() {
      window.onresize = () => {
        this.echartInstance.resize();
      };

      //根据当前容器的尺寸变化而让图表也跟着变化
      let changeSize = elementResizeDetectorMaker();
      changeSize.listenTo(this.$refs.salesChange, () => {
        this.$nextTick(() => {
          this.echartInstance.resize();
        });
      });
    },
  },
};
</script>

<style lang="less" scoped>
.panel {
  position: relative;
  height: 3.75rem;
  border: 1px solid rgba(25, 186, 139, 0.17);
  background: url(@/assets/line.png) rgba(255, 255, 255, 0.03);
  padding: 0 0.1875rem 0.5rem;
  margin-bottom: 0.1875rem;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 10px;
    height: 10px;
    border-left: 2px solid #02a6b5;
    border-top: 2px solid #02a6b5;
  }
  &::after {
    position: absolute;
    top: 0;
    right: 0;
    width: 10px;
    height: 10px;
    border-right: 2px solid #02a6b5;
    border-top: 2px solid #02a6b5;
    content: "";
  }
  .panel-footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    min-width: 3.75rem;
    &::before {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 10px;
      height: 10px;
      border-left: 2px solid #02a6b5;
      border-bottom: 2px solid #02a6b5;
      content: "";
    }
    &::after {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 10px;
      height: 10px;
      border-right: 2px solid #02a6b5;
      border-bottom: 2px solid #02a6b5;
      content: "";
    }
  }
  h2 {
    height: 0.6rem;
    color: #fff;
    line-height: 0.6rem;
    text-align: center;
    font-size: 0.25rem;
    font-weight: 400;
  }
}
</style>
